<template>
  <div class="changenumber padd">
        <div v-if="!isChange" class="yanzheng">
            <h1 class="large_t" v-if="isVerification">手机验证</h1>
            <h1 class="large_t" v-if="!isVerification">新的号码</h1>
            <ul>
                <li>
                    <input type="text" v-if="isVerification" readonly value="186****6666" class="two_t_h">
                    <input type="tel" v-model="telphone" :readonly="readonly" v-if="!isVerification" class="two_t_h" placeholder="新的手机号码">
                    <span class="iconfont icon-guanbi ico" @click="telphone=''" v-show="!isVerification&&telphone.length&&!readonly"></span>
                </li>
                <li class="clearfix">
                    <input type="tel" placeholder="验证码" class="two_t_h">
                    <span class="two_t_m fr" v-show="!isChecking&&isVerification" @click="sendCode()">| 发送验证码</span>
                    <span class="two_t_m fr" v-show="!isChecking&&!isVerification" @click="sendCode()" :class="{grey:!isClick}">| 发送验证码</span>
                    <span class="two_t_m fr grey" v-show="isChecking">| {{seconds}}s 后重试</span>
                </li>
            </ul>
            <p  v-if="isVerification">
                <span class="three_t_l">* 若当前号码已无法使用，请您</span>
                <span class="three_t_h">联系客服</span>
            </p>
            <button v-if="isVerification" @click="nextStep()" class="bluebtn">下一步</button>
            <button v-if="!isVerification" @click="isChange=true" class="bluebtn">提交</button>
        </div>
        <div v-if="isChange" class="isChange">
            <p class="success iconfont icon-chenggong"></p>
            <h2 class="one_t tc">手机号码更改成功</h2>
            <button class="bluebtn geren" @click="$router.push('/personalCenter/personalPage')">查看个人资料</button>
            <button class="whitebtn" @click="$router.push('/home/today')">返回商城首页</button>
        </div>
  </div>
</template>

<script>
export default {
  name: "Changenumber",
  data() {
    return {
      isVerification: true,
      isChange: false,
      seconds: 59,
      isChecking: false,
      telphone: '',
      timer1: '',
      isClick: false,
      readonly: false
    };
  },
  methods: {
    sendCode() {
      if (this.isVerification) {
        // 验证原有手机
        this.isChecking = true;
        this.timer1 = setInterval(() => {
          if (this.seconds == 0) {
            this.isChecking = false;
            this.seconds = 59;
            clearInterval(this.timer1);
          } else {
            this.seconds--;
          }
        }, 1000);
      } else {
        // 验证新手机
        console.log("验证新手机")
        if(this.isClick) {
          this.isChecking = true
          this.isClick = false
          this.readonly = true;
          console.log(111)
          let timer2 = setInterval(()=>{
            if(this.seconds == 0) {
              this.isChecking = false
              this.seconds = 59;
               console.log("定时器里面")
               this.isClick = true
               this.readonly = false
              clearInterval(timer2);
            }else {
              this.seconds--;
            }
          },1000)
        }
      }
    },
    nextStep() {
      this.isVerification = false
      this.isChecking = false
      clearInterval(this.timer1)
      this.seconds = 59;
    }
  },
  watch: {
    telphone: function(n, o) {
      if (/^1[34578]\d{9}$/.test(n)) {
        this.isClick = true;
      } else {
        this.isClick = false;
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.changenumber {
  font-size: 0.16rem;
  .yanzheng {
    ul {
      li {
        height: 0.52rem;
        border-bottom: 0.01rem solid #f2f2f2;
        line-height: 0.51rem;
        position: relative;
        input {
          border: none;
        }
        .grey {
          color: #ccc;
        }
        .ico {
          position: absolute;
          top: 0.03rem;
          right: 0.1rem;
        }
      }
    }
    p {
      margin: 0.15rem 0 0rem 0;
    }
    button {
      margin-top: 0.3rem;
    }
  }
  .isChange {
    p {
      width: 0.93rem;
      height: 0.93rem;
      font-size: 0.93rem;
      margin: 0 auto;
      margin-top: 0.3rem;
      margin-bottom: 0.33rem;
    }
    .geren {
      margin: 0.3rem 0rem 0.15rem 0rem;
    }
  }
}
</style>



