export default {
  name: "Changenumber",
  data() {
    return {
      isVerification: true,
      isChange: false,
      seconds: 59,
      isChecking: false,
      telphone: '',
      timer1: '',
      isClick: false,
      readonly: false
    };
  },
  methods: {
    sendCode() {
      if (this.isVerification) {
        // 验证原有手机
        this.isChecking = true;
        this.timer1 = setInterval(() => {
          if (this.seconds == 0) {
            this.isChecking = false;
            this.seconds = 59;
            clearInterval(this.timer1);
          } else {
            this.seconds--;
          }
        }, 1000);
      } else {
        // 验证新手机
        console.log("验证新手机")
        if(this.isClick) {
          this.isChecking = true
          this.isClick = false
          this.readonly = true;
          console.log(111)
          let timer2 = setInterval(()=>{
            if(this.seconds == 0) {
              this.isChecking = false
              this.seconds = 59;
               console.log("定时器里面")
               this.isClick = true
               this.readonly = false
              clearInterval(timer2);
            }else {
              this.seconds--;
            }
          },1000)
        }
      }
    },
    nextStep() {
      this.isVerification = false
      this.isChecking = false
      clearInterval(this.timer1)
      this.seconds = 59;
    }
  },
  watch: {
    telphone: function(n, o) {
      if (/^1[34578]\d{9}$/.test(n)) {
        this.isClick = true;
      } else {
        this.isClick = false;
      }
    }
  },
  components: {}
};



